.list-card {
  flex-wrap: wrap;
  gap: 20px;
  margin: 0;
  padding: 20px;
  list-style: none;
  display: flex;
}

.card-item {
  width: calc(16.6667% - 16.6667px);
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, .3);
  border-radius: 4px;
}

.card-item .img {
  width: 100%;
}

.disc {
  padding: 10px;
}

.title {
  font-size: 14px;
}

.btn {
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(0, 0, 0, .3);
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  padding: 5px;
  display: flex;
  position: absolute;
  top: 5px;
  right: 27px;
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  transform-origin: center;
  width: 528px;
  min-height: 581px;
  background-color: #fff;
  border-radius: 4px;
  padding: 40px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(0);
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.no-scroll {
  overflow: hidden;
}

.btn-close {
  width: 30px;
  height: 30px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  outline: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.list-basket {
  margin: 0;
  padding: 20px;
  list-style: none;
}

.list-basket-item {
  justify-content: space-around;
  align-items: center;
  display: flex;
}

